"use client";

import { useEffect, useState } from 'react';
import { useAuth } from '../../../context/authContext';
import styles from '../../../styles/estudiante-horario.module.css';

export default function HorarioPage() {
  const { user } = useAuth();
  const [horario, setHorario] = useState<any[]>([]);

  useEffect(() => {
    if (!user) return;
    // Llamar endpoint que devuelva el horario del estudiante (a implementar)
    fetch(`/api/estudiante/horario?firebaseUid=${user.uid}`)
      .then(r => r.json())
      .then(data => setHorario(Array.isArray(data) ? data : []));
  }, [user]);

  return (
    <div className={styles.container}>
      <h2 className={styles.title}>Horario Escolar</h2>
      {horario.length === 0 && <p className={styles.empty}>No hay horario disponible.</p>}
      {horario.length > 0 && (
        <table className={styles.table}>
          <thead>
            <tr><th>Dia</th><th>Hora</th><th>Materia</th><th>Aula</th></tr>
          </thead>
          <tbody>
            {horario.map((h, i) => (
              <tr key={i}>
                <td>{h.dia}</td>
                <td>{h.hora}</td>
                <td>{h.materia}</td>
                <td>{h.aula}</td>
              </tr>
            ))}
          </tbody>
        </table>
      )}
    </div>
  );
}
